function StatusDemo() {
  const [status, setStatus] = useState("Available");
  return (
    <div>
      <h1>Employee Status : {status}</h1>
      <>
        <button onClick={() => setStatus("Away")}> Away</button>
        <br />
        <button onClick={() => setStatus("Available")}>Available</button>
      </>
    </div>
  );
}

